@import url("https://fonts.googleapis.com/css2?family=Beth+Ellen&display=swap");
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@500&display=swap');

$title-font: "Beth Ellen", cursive;

* {
  font-family: 'EB Garamond',Georgia, 'Times New Roman', Times, serif; }

%titles {
  font-family: $title-font; }

.title {
  @extend %titles;
  font-size: 40px; }

.page-title {
  @extend %titles;
  font-size: 20px; }

.sub-title {
  font-size: 30px; }

.information-text {
  color: grey;
  font-size: 14px;
  margin: 15px 0; }

.info-box {
  color: white;
  background-color: #a74e05;
  font-size: 15px;
  text-align: center;
  padding: 10px;
  margin-bottom: 10px; }
