/** SIZES **/
$nav-height : 20px;
$footer-height : 20px;
$padding : 20px;
$margin : 20px;
// dynamic margins
@for $i from 1 through 3 {
  .mg-#{$i} {
    margin: 0 ($i * .25rem); } }

/** BORDERS **/
$border-soft : 5px;
$border-pill : 20px;
$border-circle : 50%;

/** COLORS **/
// TODO: adapt
$primary : #222222;
$light: whitesmoke;
$caramel: #fda708;
$danger : #a80000;
$bg-blue : blue;
$light-grey : #CCC;

/** SHADOWS **/
$shadow-soft : 0 1px 6px 0 rgba(32, 33, 36, .28);
$shadow-hard : 7px 7px 7px rgba(0,0,0,0.3);
