.flex-row {
  display: flex;
  align-items: center;

  &.sp-btw {
    justify-content: space-between; }

  &.sp-around {
    justify-content: space-around; }

  &.sp-center {
    justify-content: center; }

  &.align-start {
    align-items: flex-start; } }

.flex-col {
  @extend .flex-row;
  flex-direction: column; }
