.AdminPage {
  padding: $padding;
  min-height: 65vh;

  .nav-link {
    @include animated-underline($caramel); }

  .table {
    width: 100%;
    border-bottom: 1px solid $caramel;
    margin: 20px auto 50px;
    &.of-employees {
      width: 60%;
      margin: 50px 50px 0 0; } }

  .row {
    border-top: 1px solid $caramel;
    &:hover {
      background-color: $caramel; }

    td {
      min-width: 100px;
      padding: 0 5px;

      &:last-child {
        text-align: right;
        min-width: 50px; } } }

  .btn-group {
    margin-top: 10px; }

  .mui-btn {
    margin: 0 0 10px; }

  .mui-btn--flat {
    background-color: #dadada; }

  $mui-delete-btn-size : 30px;

  .mui-btn.delete {
    padding: 0;
    margin: 2px;
    width: $mui-delete-btn-size;
    height: $mui-delete-btn-size;
    line-height: $mui-delete-btn-size; }

  .mui-btn.edit {
    background-color: $caramel;
    &:hover {
      color: #fff; } } }

.EmployeesList.AdminPage, .ReservationList.AdminPage {
  position: relative;

  .info-box {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%); } }
